/* Modern Portfolio CSS with Dark Mode Support */

/* CSS Variables for theming */
:root {
  /* Light Theme Colors */
  --primary-color: #6366f1;
  --primary-hover: #4f46e5;
  --secondary-color: #64748b;
  --accent-color: #10b981;
  --warning-color: #f59e0b;
  --error-color: #ef4444;
  --info-color: #06b6d4;
  
  /* Background Colors */
  --bg-primary: #ffffff;
  --bg-secondary: #f8fafc;
  --bg-tertiary: #f1f5f9;
  --bg-card: #ffffff;
  
  /* Text Colors */
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --text-muted: #94a3b8;
  --text-inverse: #ffffff;
  
  /* Border Colors */
  --border-color: #e2e8f0;
  --border-hover: #cbd5e1;
  
  /* Shadow */
  --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
  
  /* Border Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
  
  /* Spacing */
  --space-xs: 0.5rem;
  --space-sm: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 3rem;
  --space-2xl: 4rem;
  
  /* Typography */
  --font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 1.875rem;
  --font-size-4xl: 2.25rem;
  --font-size-5xl: 3rem;
  
  /* Transitions */
  --transition-fast: 150ms ease;
  --transition-normal: 250ms ease;
  --transition-slow: 350ms ease;
}

/* Dark Mode Variables */
.dark-mode {
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --bg-card: #1e293b;
  
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --text-muted: #64748b;
  
  --border-color: #334155;
  --border-hover: #475569;
}

/* Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Full width container */
.container {
  width: 100%;
  max-width: none;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-family);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  transition: background-color var(--transition-normal), color var(--transition-normal);
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  color: var(--text-primary);
  font-weight: 600;
  line-height: 1.2;
}

h1 { font-size: var(--font-size-5xl); }
h2 { font-size: var(--font-size-4xl); }
h3 { font-size: var(--font-size-3xl); }
h4 { font-size: var(--font-size-2xl); }
h5 { font-size: var(--font-size-xl); }
h6 { font-size: var(--font-size-lg); }

p {
  color: var(--text-secondary);
  font-size: var(--font-size-base);
  line-height: 1.7;
}

/* Modern Navbar Styles */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 5%;
  z-index: 1000;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  background: rgba(15, 23, 42, 0.8);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.header.scrolled {
  padding: 1rem 5%;
  background: rgba(15, 23, 42, 0.95);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(90deg, #60a5fa, #818cf8, #a78bfa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  cursor: pointer;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
}

.logo:hover {
  transform: translateY(-1px);
  text-shadow: 0 2px 10px rgba(96, 165, 250, 0.3);
}

.nav {
  display: flex;
  gap: 1.5rem;
  align-items: center;
}

.nav-link {
  color: #e2e8f0;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
  position: relative;
  padding: 0.5rem 0;
  transition: all 0.3s ease;
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #60a5fa, #818cf8);
  transition: width 0.3s ease;
  border-radius: 2px;
}

.nav-link:hover::after,
.nav-link:focus::after {
  width: 100%;
}

.nav-link:hover {
  color: #ffffff;
  transform: translateY(-1px);
}

.contact-btn {
  background: linear-gradient(135deg, #8b5cf6, #6366f1);
  color: white !important;
  padding: 0.6rem 1.75rem;
  border-radius: 50px;
  font-weight: 500;
  letter-spacing: 0.5px;
  border: none;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  box-shadow: 0 4px 15px rgba(99, 102, 241, 0.3);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.contact-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: linear-gradient(135deg, #6366f1, #8b5cf6);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: -1;
  border-radius: 50px;
}

.contact-btn:hover::before {
  width: 100%;
}

.contact-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.4);
}

.theme-toggle {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: #e2e8f0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.theme-toggle:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: rotate(30deg) scale(1.05);
  color: #ffffff;
}

/* Mobile menu button */
.mobile-menu-btn {
  display: none;
  background: none;
  border: none;
  color: #e2e8f0;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 1001;
}

/* Responsive styles */
@media (max-width: 768px) {
  .mobile-menu-btn {
    display: block;
  }
  
  .nav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 70%;
    height: 100vh;
    background: rgba(15, 23, 42, 0.98);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    transition: right 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 1000;
    padding: 2rem;
    box-shadow: -10px 0 30px rgba(0, 0, 0, 0.2);
  }
  
  .nav.active {
    right: 0;
  }
  
  .nav-link {
    font-size: 1.2rem;
    padding: 0.75rem 0;
  }
  
  .contact-btn {
    margin-top: 1rem;
    padding: 0.8rem 2rem;
  }
}

/* Enhanced Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 0 5%;
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  color: #e2e8f0;
}

.hero-container {
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
  z-index: 2;
  padding: 6rem 0;
}

/* Hero Blur Effects */
.hero-blur {
  position: absolute;
  width: 500px;
  height: 500px;
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.2) 0%, rgba(168, 85, 247, 0.2) 100%);
  filter: blur(100px);
  border-radius: 50%;
  z-index: 1;
  top: -200px;
  right: -200px;
  opacity: 0.6;
}

.hero-blob {
  position: absolute;
  width: 600px;
  height: 600px;
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.1) 0%, rgba(168, 85, 247, 0.1) 100%);
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  animation: blob-animation 20s infinite alternate;
  z-index: 1;
  bottom: -200px;
  left: -200px;
  opacity: 0.4;
}

@keyframes blob-animation {
  0% {
    transform: rotate(0deg) scale(1);
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  }
  50% {
    border-radius: 50% 50% 20% 80% / 25% 80% 20% 75%;
  }
  100% {
    transform: rotate(360deg) scale(1.1);
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
}

/* Hero Content */
.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(99, 102, 241, 0.15);
  color: #818cf8;
  padding: 0.5rem 1rem;
  border-radius: 50px;
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 1.5rem;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(99, 102, 241, 0.2);
  animation: float 6s ease-in-out infinite;
}

.waving-hand {
  font-size: 1.2rem;
  animation: wave 2s infinite;
  display: inline-block;
  transform-origin: 70% 70%;
}

@keyframes wave {
  0% { transform: rotate(0deg); }
  10% { transform: rotate(14deg); }
  20% { transform: rotate(-8deg); }
  30% { transform: rotate(14deg); }
  40% { transform: rotate(-4deg); }
  50% { transform: rotate(10deg); }
  60% { transform: rotate(0deg); }
  100% { transform: rotate(0deg); }
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 1rem;
  background: linear-gradient(90deg, #e0e7ff, #c7d2fe, #a5b4fc);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

.text-gradient {
  background: linear-gradient(90deg, #818cf8, #a78bfa);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
  position: relative;
  display: inline-block;
}

.hero-subtitle {
  font-size: 1.8rem;
  font-weight: 600;
  color: #94a3b8;
  margin-bottom: 1.5rem;
  line-height: 1.4;
}

.hero-description {
  font-size: 1.1rem;
  color: #94a3b8;
  margin-bottom: 2.5rem;
  line-height: 1.8;
  max-width: 90%;
}

/* Stats */
.hero-stats {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 2.5rem;
  flex-wrap: wrap;
}

.stat-card {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1.2rem 1.5rem;
  min-width: 120px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: default;
}

.stat-card:hover {
  transform: translateY(-5px);
  background: rgba(99, 102, 241, 0.1);
  border-color: rgba(99, 102, 241, 0.2);
  box-shadow: 0 10px 30px -10px rgba(99, 102, 241, 0.2);
}

.stat-value {
  display: block;
  font-size: 1.8rem;
  font-weight: 700;
  background: linear-gradient(90deg, #818cf8, #a78bfa);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0.25rem;
  line-height: 1;
}

.stat-label {
  font-size: 0.9rem;
  color: #94a3b8;
  font-weight: 500;
  letter-spacing: 0.5px;
}

/* Buttons */
.hero-actions {
  display: flex;
  gap: 1rem;
  margin-bottom: 2.5rem;
  flex-wrap: wrap;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.9rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  border: none;
  outline: none;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.2));
}

.btn:hover::before {
  opacity: 1;
}

.btn-primary {
  background: linear-gradient(135deg, #818cf8, #a78bfa);
  color: white;
  box-shadow: 0 4px 15px rgba(99, 102, 241, 0.3);
}

.btn-outline {
  background: transparent;
  color: #e2e8f0;
  border: 1px solid #334155;
  transition: all 0.3s ease;
}

.btn-outline:hover {
  border-color: #818cf8;
  color: #818cf8;
  background: rgba(129, 140, 248, 0.1);
}

.btn-arrow {
  margin-left: 0.5rem;
  display: inline-flex;
  transition: transform 0.3s ease;
}

.btn:hover .btn-arrow {
  transform: translateX(4px);
}

/* Social Icons */
.hero-socials {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.03);
  color: #94a3b8;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(255, 255, 255, 0.05);
  font-size: 1.2rem;
}

.social-link:hover {
  background: rgba(99, 102, 241, 0.1);
  color: #818cf8;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(99, 102, 241, 0.2);
  border-color: rgba(99, 102, 241, 0.2);
}

.social-link svg {
  width: 20px;
  height: 20px;
}

/* Hero Image */
.hero-image-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.profile-card {
  position: relative;
  z-index: 2;
  perspective: 1000px;
}

.profile-image-wrapper {
  position: relative;
  width: 350px;
  height: 350px;
  border-radius: 30px;
  overflow: hidden;
  box-shadow: 0 20px 50px -10px rgba(0, 0, 0, 0.3);
  border: 10px solid rgba(255, 255, 255, 0.05);
  transform-style: preserve-3d;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.availability-badge {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(90deg, #818cf8, #a78bfa);
  color: white;
  padding: 0.6rem 1.5rem;
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 500;
  box-shadow: 0 10px 30px -5px rgba(99, 102, 241, 0.3);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  z-index: 3;
  white-space: nowrap;
}

.availability-badge::before {
  content: '';
  width: 8px;
  height: 8px;
  background: #4ade80;
  border-radius: 50%;
  animation: pulse 2s infinite;
  display: inline-block;
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(74, 222, 128, 0.7); }
  70% { box-shadow: 0 0 0 10px rgba(74, 222, 128, 0); }
  100% { box-shadow: 0 0 0 0 rgba(74, 222, 128, 0); }
}

/* Floating Shapes */
.floating-shapes {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
  overflow: hidden;
}

.shape {
  position: absolute;
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.1), rgba(168, 85, 247, 0.1));
  border-radius: 20px;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.shape-1 {
  width: 100px;
  height: 100px;
  top: 10%;
  left: 10%;
  animation-delay: 0s;
}

.shape-2 {
  width: 150px;
  height: 150px;
  bottom: 10%;
  right: 10%;
  border-radius: 30px;
  animation-delay: 1s;
}

.shape-3 {
  width: 60px;
  height: 60px;
  top: 50%;
  right: 15%;
  border-radius: 15px;
  animation-delay: 2s;
}

.shape-4 {
  width: 80px;
  height: 80px;
  bottom: 20%;
  left: 20%;
  border-radius: 25px;
  animation-delay: 3s;
}

.shape-5 {
  width: 120px;
  height: 120px;
  top: 20%;
  right: 20%;
  border-radius: 35px;
  animation-delay: 4s;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .hero-container {
    grid-template-columns: 1.1fr 1fr;
    gap: 3rem;
  }
  
  .hero-title {
    font-size: 3rem;
  }
  
  .profile-image-wrapper {
    width: 320px;
    height: 320px;
  }
}

@media (max-width: 992px) {
  .hero-container {
    grid-template-columns: 1fr;
    text-align: center;
    padding: 8rem 0;
  }
  
  .hero-content {
    max-width: 700px;
    margin: 0 auto;
  }
  
  .hero-description {
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  
  .hero-stats {
    justify-content: center;
  }
  
  .hero-actions {
    justify-content: center;
  }
  
  .hero-socials {
    justify-content: center;
  }
  
  .profile-card {
    margin: 3rem auto 0;
  }
}

@media (max-width: 768px) {
  .hero {
    padding: 0 1.5rem;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.5rem;
  }
  
  .hero-description {
    font-size: 1rem;
  }
  
  .stat-card {
    padding: 1rem;
    min-width: 100px;
  }
  
  .stat-value {
    font-size: 1.5rem;
  }
  
  .btn {
    padding: 0.8rem 1.5rem;
    font-size: 0.95rem;
  }
  
  .profile-image-wrapper {
    width: 280px;
    height: 280px;
  }
}

@media (max-width: 576px) {
  .hero-title {
    font-size: 2.2rem;
  }
  
  .hero-subtitle {
    font-size: 1.3rem;
  }
  
  .hero-stats {
    gap: 1rem;
  }
  
  .hero-actions {
    flex-direction: column;
    align-items: center;
  }
  
  .btn {
    width: 100%;
    max-width: 250px;
  }
  
  .profile-image-wrapper {
    width: 250px;
    height: 250px;
  }
  
  .availability-badge {
    font-size: 0.8rem;
    padding: 0.5rem 1.2rem;
  }
}

/* Section Styles */
section {
  padding: var(--space-2xl) var(--space-xl);
  width: 100%;
}

.section-header {
  text-align: center;
  margin-bottom: var(--space-2xl);
}

.section-subtitle {
  color: var(--text-muted);
  font-size: var(--font-size-lg);
  margin-top: var(--space-sm);
}

/* About Section */
.about-content {
  display: grid;
  gap: var(--space-xl);
}

.about-text p {
  font-size: var(--font-size-lg);
  margin-bottom: var(--space-lg);
}

.about-highlights {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-lg);
}

.highlight-item {
  display: flex;
  gap: var(--space-md);
  padding: var(--space-lg);
  background-color: var(--bg-card);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-color);
  transition: all var(--transition-normal);
}

.highlight-item:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
  border-color: var(--primary-color);
}

.highlight-icon {
  font-size: var(--font-size-3xl);
  flex-shrink: 0;
}

.highlight-item h4 {
  margin-bottom: var(--space-xs);
  color: var(--text-primary);
}

.highlight-item p {
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
}

/* Experience Section */
.timeline {
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

/* Career Section - combined horizontal layout */
.career {
  background-color: var(--bg-secondary);
}

/* Group headings for education and experience */
.career-group { margin-bottom: var(--space-2xl); }
.career-group-title {
  margin-left: var(--space-xl);
  margin-bottom: var(--space-sm);
  color: var(--text-primary);
  font-size: var(--font-size-xl);
}

/* Horizontal timeline layout (like screenshot) */
.career-timeline {
  position: relative;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: minmax(220px, 1fr);
  gap: var(--space-xl);
  align-items: start;
  padding: var(--space-xl) var(--space-xl) var(--space-lg);
}

.career-timeline::before {
  content: '';
  position: absolute;
  top: 42px;
  left: var(--space-xl);
  right: calc(var(--space-xl) + 16px);
  height: 2px;
  background: var(--text-muted);
  opacity: 0.4;
}

.career-timeline::after {
  content: 'âžœ';
  position: absolute;
  top: 29px;
  right: var(--space-xl);
  color: var(--text-muted);
  font-size: 18px;
}

/* Color accents per rail */
.career-timeline.education::before { background: var(--primary-color); opacity: 0.35; }
.career-timeline.education::after { color: var(--primary-color); }
.career-timeline.experience::before { background: var(--accent-color); opacity: 0.35; }
.career-timeline.experience::after { color: var(--accent-color); }

.career-step {
  position: relative;
}

.step-marker {
  width: 48px;
  height: 48px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  border: 3px solid var(--bg-primary);
  background-color: var(--bg-card);
  box-shadow: var(--shadow-md);
}

.step-marker.education { outline: 2px solid var(--primary-color); }
.step-marker.work { outline: 2px solid var(--accent-color); }

.step-emoji { font-size: 22px; }

.step-content {
  margin-top: var(--space-md);
}

.step-title {
  color: var(--text-primary);
  margin-bottom: 2px;
}

.step-subtitle {
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
}

.step-period {
  background-color: var(--bg-tertiary);
  color: var(--text-secondary);
  border-radius: var(--radius-md);
  padding: 2px 8px;
  display: inline-block;
  margin-top: 6px;
  font-size: var(--font-size-xs);
  font-weight: 600;
}

@media (max-width: 900px) {
  .career-timeline {
    grid-auto-columns: minmax(220px, 260px);
    overflow-x: auto;
    padding-bottom: var(--space-xl);
  }
}

/* Journey Section - horizontal timeline */
.journey {
  background-color: var(--bg-secondary);
}

.journey-track {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, minmax(180px, 1fr));
  gap: var(--space-xl);
  align-items: start;
  padding: 0 var(--space-xl);
}

.journey-track::after {
  content: '';
  position: absolute;
  top: 34px;
  left: var(--space-xl);
  right: var(--space-xl);
  height: 2px;
  background-color: var(--text-muted);
  opacity: 0.4;
}

.journey-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.journey-node {
  position: relative;
  width: 48px;
  height: 48px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  border: 3px solid var(--bg-primary);
  background-color: var(--bg-card);
  box-shadow: var(--shadow-md);
  z-index: 1;
}

.journey-node.education {
  outline: 2px solid var(--primary-color);
}

.journey-node.work {
  outline: 2px solid var(--accent-color);
}

.journey-emoji {
  font-size: 22px;
}

.journey-card {
  margin-top: var(--space-md);
}

.journey-title {
  color: var(--text-primary);
  margin-bottom: 2px;
}

.journey-subtitle {
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
}

.journey-period {
  background-color: var(--bg-tertiary);
  color: var(--text-secondary);
  border-radius: var(--radius-md);
  padding: 2px 8px;
  display: inline-block;
  margin-top: 6px;
  font-size: var(--font-size-xs);
  font-weight: 600;
}

@media (max-width: 900px) {
  .journey-track {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 520px) {
  .journey-track {
    grid-template-columns: 1fr;
  }

  .journey-track::after {
    display: none;
  }
}

.timeline::before {
  content: '';
  position: absolute;
  left: 20px;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: var(--primary-color);
}

.timeline-item {
  position: relative;
  margin-bottom: var(--space-xl);
  padding-left: 60px;
}

.timeline-marker {
  position: absolute;
  left: 11px;
  top: 0;
  width: 20px;
  height: 20px;
  background-color: var(--primary-color);
  border-radius: 50%;
  border: 4px solid var(--bg-primary);
}

.timeline-content {
  background-color: var(--bg-card);
  padding: var(--space-lg);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-color);
  transition: all var(--transition-normal);
}

.timeline-content:hover {
  transform: translateX(8px);
  box-shadow: var(--shadow-lg);
}

.timeline-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: var(--space-sm);
}

.timeline-header h3 {
  color: var(--text-primary);
  margin-bottom: var(--space-xs);
}

.timeline-period {
  background-color: var(--primary-color);
  color: var(--text-inverse);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--font-size-sm);
  font-weight: 500;
}

.timeline-company {
  color: var(--accent-color);
  font-weight: 600;
  margin-bottom: var(--space-sm);
}

.timeline-description {
  margin-bottom: var(--space-md);
  color: var(--text-secondary);
}

.timeline-tech {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-xs);
}

/* Skills Section */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-lg);
}

.skill-category {
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-lg);
  padding: var(--space-lg);
  transition: all var(--transition-normal);
}

.skill-category:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
  border-color: var(--primary-color);
}

.skill-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-lg);
  padding-bottom: var(--space-sm);
  border-bottom: 2px solid var(--primary-color);
}

.skill-header h3 {
  color: var(--text-primary);
  margin: 0;
}

.skill-count {
  background-color: var(--primary-color);
  color: var(--text-inverse);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--font-size-sm);
  font-weight: 500;
}

.skill-items {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.skill-item {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}

.skill-name {
  font-weight: 500;
  color: var(--text-primary);
}

.skill-bar {
  height: 6px;
  background-color: var(--bg-tertiary);
  border-radius: var(--radius-sm);
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--accent-color));
  border-radius: var(--radius-sm);
  transition: width 1s ease;
}

/* Projects Section */
.projects-section {
  padding: 8rem 5%;
  background-color: var(--bg-primary);
  position: relative;
  overflow: hidden;
}

.projects-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 20% 30%, rgba(99, 102, 241, 0.1) 0%, transparent 30%),
              radial-gradient(circle at 80% 70%, rgba(236, 72, 153, 0.1) 0%, transparent 30%);
  z-index: 0;
  opacity: 0.5;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
  position: relative;
  z-index: 1;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.section-header h2 {
  font-size: 2.75rem;
  margin: 0.5rem 0 1.5rem;
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
  line-height: 1.2;
}

.section-subtitle {
  color: var(--primary-color);
  font-weight: 600;
  font-size: 1.1rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
  display: block;
  opacity: 0.9;
}

.section-divider {
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  margin: 1.5rem auto;
  border-radius: 2px;
}

.section-description {
  color: var(--text-secondary);
  font-size: 1.1rem;
  line-height: 1.7;
  max-width: 700px;
  margin: 0 auto;
  opacity: 0.9;
}

.project-filters {
  margin-bottom: 3rem;
  position: relative;
  z-index: 2;
}

.filter-group {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin: 0 auto;
  max-width: 600px;
}

.filter-btn {
  padding: 0.7rem 1.75rem;
  border: 1px solid var(--border-color);
  background: transparent;
  color: var(--text-secondary);
  border-radius: 100px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.filter-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(99, 102, 241, 0.1), rgba(139, 92, 246, 0.1));
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.filter-btn:hover::before {
  opacity: 1;
}

.filter-btn .filter-icon {
  font-size: 1.1em;
  transition: transform 0.3s ease;
}

.filter-btn:hover .filter-icon {
  transform: scale(1.1);
}

.filter-btn.active {
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  color: white;
  border-color: transparent;
  box-shadow: 0 4px 15px rgba(99, 102, 241, 0.3);
}

.filter-btn.active::before {
  display: none;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
  gap: 2.5rem;
  position: relative;
  z-index: 1;
  margin-bottom: 4rem;
}

.project-card {
  background: var(--bg-card);
  border-radius: 1.25rem;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  border: 1px solid var(--border-color);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.01));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border-color: rgba(99, 102, 241, 0.3);
}

.project-card:hover::before {
  opacity: 1;
}

.project-card.featured {
  border: 1px solid rgba(99, 102, 241, 0.3);
  box-shadow: 0 15px 35px rgba(99, 102, 241, 0.15);
}

.project-card-inner {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--bg-card);
  border-radius: 1.25rem;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.project-image-container {
  position: relative;
  overflow: hidden;
  border-radius: 0.75rem;
  margin: 1.25rem;
  margin-bottom: 0;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  height: 220px;
}

.project-image {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  position: relative;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 0.75rem;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-links {
  display: flex;
  gap: 1rem;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 0 1.5rem;
}

.project-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 50px;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.3s ease;
  width: 100%;
  max-width: 180px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.project-link.demo {
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  border: none;
}

.project-link .link-icon {
  margin-left: 0.5rem;
  transition: transform 0.3s ease;
}

.project-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.project-link:hover .link-icon {
  transform: translateX(3px);
}

.featured-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  color: white;
  padding: 0.35rem 1rem;
  border-radius: 50px;
  font-size: 0.8rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  z-index: 2;
  box-shadow: 0 4px 15px rgba(99, 102, 241, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.star-icon {
  font-size: 0.9em;
  color: #ffd700;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.project-content {
  padding: 1.75rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.project-header h3 {
  font-size: 1.4rem;
  margin: 0;
  color: var(--text-primary);
  line-height: 1.3;
  flex: 1;
}

.project-links-mobile {
  display: none;
  gap: 0.5rem;
}

.project-links-mobile .project-link {
  width: auto;
  padding: 0.4rem 0.8rem;
  font-size: 0.85rem;
  max-width: none;
}

.project-description {
  color: var(--text-secondary);
  font-size: 1rem;
  line-height: 1.7;
  margin-bottom: 1.5rem;
  flex: 1;
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: auto;
}

.tech-tag {
  background: var(--bg-tertiary);
  color: var(--text-secondary);
  padding: 0.3rem 0.9rem;
  border-radius: 50px;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-color);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.tech-tag:hover {
  transform: translateY(-2px);
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.project-glow {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 0%, rgba(99, 102, 241, 0.4) 0%, transparent 70%);
  border-radius: 1.25rem;
  z-index: -1;
  pointer-events: none;
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.view-more {
  text-align: center;
  margin-top: 3rem;
}

.view-more-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  color: white;
  padding: 1rem 2rem;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 15px rgba(99, 102, 241, 0.3);
  border: none;
  cursor: pointer;
  font-size: 1.05rem;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.view-more-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #8b5cf6, #6366f1);
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.view-more-btn:hover::before {
  opacity: 1;
}

.arrow-icon {
  transition: transform 0.3s ease;
}

.view-more-btn:hover .arrow-icon {
  transform: translateX(5px);
}

/* Dark Mode Specific Styles */
.dark-mode .project-card {
  background: rgba(30, 41, 59, 0.6);
  border: 1px solid rgba(51, 65, 85, 0.5);
}

.dark-mode .project-card:hover {
  border-color: rgba(99, 102, 241, 0.5);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.dark-mode .project-card.featured {
  border: 1px solid rgba(99, 102, 241, 0.5);
  box-shadow: 0 15px 40px rgba(99, 102, 241, 0.15);
}

.dark-mode .tech-tag {
  background: rgba(30, 41, 59, 0.8);
  border: 1px solid rgba(71, 85, 105, 0.5);
}

.dark-mode .tech-tag:hover {
  background: rgba(30, 41, 59, 1);
  border-color: var(--primary-color);
}

/* Responsive Design */
@media (max-width: 1200px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    gap: 2rem;
  }
}

@media (max-width: 1024px) {
  .projects-section {
    padding: 6rem 5%;
  }
  
  .section-header h2 {
    font-size: 2.5rem;
  }
  
  .projects-grid {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

@media (max-width: 768px) {
  .projects-section {
    padding: 5rem 5%;
  }
  
  .section-header {
    margin-bottom: 3rem;
  }
  
  .section-header h2 {
    font-size: 2.25rem;
  }
  
  .section-description {
    font-size: 1.05rem;
  }
  
  .filter-group {
    gap: 0.75rem;
  }
  
  .filter-btn {
    padding: 0.6rem 1.5rem;
    font-size: 0.95rem;
  }
  
  .projects-grid {
    gap: 1.75rem;
  }
  
  .project-links-mobile {
    display: flex;
  }
  
  .project-overlay {
    display: none;
  }
  
  .project-content {
    padding: 1.5rem;
  }
  
  .project-header h3 {
    font-size: 1.3rem;
  }
  
  .project-description {
    font-size: 0.95rem;
  }
}

@media (max-width: 576px) {
  .projects-section {
    padding: 4rem 5%;
  }
  
  .section-header h2 {
    font-size: 2rem;
  }
  
  .section-subtitle {
    font-size: 1rem;
  }
  
  .section-description {
    font-size: 1rem;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .project-image-container {
    height: 200px;
    margin: 1rem;
    margin-bottom: 0;
  }
  
  .project-content {
    padding: 1.25rem;
  }
  
  .view-more-btn {
    width: 100%;
    padding: 0.9rem 1.5rem;
    font-size: 1rem;
  }
}

@media (max-width: 400px) {
  .project-header {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .project-links-mobile {
    width: 100%;
    justify-content: flex-start;
  }
  
  .project-links-mobile .project-link {
    flex: 1;
    justify-content: center;
  }
}

/* Contact Section */
.contact-content-centered {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.contact-info h3 {
  margin-bottom: var(--space-md);
  color: var(--text-primary);
}

.contact-info p {
  margin-bottom: var(--space-xl);
  color: var(--text-secondary);
}

.contact-methods {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

.contact-method {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-md);
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-lg);
  transition: all var(--transition-normal);
}

.contact-method:hover {
  transform: translateX(8px);
  box-shadow: var(--shadow-md);
  border-color: var(--primary-color);
}

.contact-icon {
  font-size: var(--font-size-2xl);
  flex-shrink: 0;
}

.contact-method h4 {
  color: var(--text-primary);
  margin-bottom: var(--space-xs);
}

.contact-method a {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  transition: color var(--transition-fast);
}

.contact-method a:hover {
  color: var(--primary-hover);
}

.contact-form-centered {
  background-color: var(--bg-card);
  padding: var(--space-xl);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-color);
  width: 100%;
  max-width: 500px;
}

.form-group {
  margin-bottom: var(--space-lg);
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: var(--space-md);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  transition: all var(--transition-fast);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

/* Loading and Status Styles */
.btn.loading {
  opacity: 0.7;
  cursor: not-allowed;
}

.loading-spinner {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: var(--space-xs);
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.success-message {
  margin-top: var(--space-md);
  padding: var(--space-md);
  background-color: var(--accent-color);
  color: var(--text-inverse);
  border-radius: var(--radius-md);
  text-align: center;
  font-weight: 500;
}

.error-message {
  margin-top: var(--space-md);
  padding: var(--space-md);
  background-color: var(--error-color);
  color: var(--text-inverse);
  border-radius: var(--radius-md);
  text-align: center;
  font-weight: 500;
}

/* Contact Section */
.contact {
  padding: 6rem 0;
  position: relative;
  overflow: hidden;
}

.contact-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 4rem;
  align-items: flex-start;
  margin-top: 3rem;
}

/* Connect Section */
.connect-section {
  padding: 2rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 16px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.connect-section h3 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  position: relative;
  display: inline-block;
}

.connect-section h3::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 50px;
  height: 3px;
  background: var(--primary-color);
  border-radius: 2px;
}

.connect-description {
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  line-height: 1.7;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 1.8rem;
}

.contact-method {
  display: flex;
  align-items: flex-start;
  gap: 1.2rem;
}

.contact-icon {
  font-size: 1.5rem;
  color: var(--primary-color);
  background: rgba(99, 102, 241, 0.1);
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.contact-method h4 {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
  color: var(--text-primary);
}

.contact-method a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact-method a:hover {
  color: var(--primary-color);
}

/* Form Section */
.form-section {
  background: rgba(255, 255, 255, 0.03);
  padding: 2.5rem;
  border-radius: 16px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  margin-bottom: 0.5rem;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.9rem 1.2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  color: var(--text-primary);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.2);
}

.form-group textarea {
  min-height: 150px;
  resize: vertical;
}

.submit-btn {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  width: 100%;
}

.submit-btn:hover:not(:disabled) {
  background: #818cf8;
  transform: translateY(-2px);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.submit-btn .spinner {
  width: 18px;
  height: 18px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
}

.status-message {
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1rem;
  font-size: 0.95rem;
  text-align: center;
}

.status-message.success {
  background: rgba(16, 185, 129, 0.1);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.2);
}

.status-message.error {
  background: rgba(239, 68, 68, 0.1);
  color: #ef4444;
  border: 1px solid rgba(239, 68, 68, 0.2);
}

/* Responsive Design */
@media (max-width: 992px) {
  .contact-container {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .connect-section,
  .form-section {
    padding: 1.8rem;
  }
}

@media (max-width: 576px) {
  .contact {
    padding: 4rem 0;
  }
  
  .contact-container {
    padding: 0 1.2rem;
  }
  
  .contact-method {
    flex-direction: column;
    gap: 0.8rem;
  }
  
  .contact-icon {
    width: 42px;
    height: 42px;
    font-size: 1.3rem;
  }
}

/* Career Section */
.career-container {
  max-width: 1000px;
  margin: 2rem auto 0;
  position: relative;
}

.career-tabs {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2.5rem;
  position: relative;
  z-index: 2;
}

.tab-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border-radius: 50px;
  border: none;
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-secondary);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.tab-btn:hover {
  background: rgba(99, 102, 241, 0.1);
  color: var(--primary-color);
}

.tab-btn.active {
  background: var(--primary-color);
  color: white;
  box-shadow: 0 4px 15px rgba(99, 102, 241, 0.3);
}

.tab-icon {
  font-size: 1.2rem;
}

.career-content {
  position: relative;
  padding: 1rem 0;
}

.career-card {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  padding: 1.75rem;
  margin-bottom: 2rem;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  overflow: hidden;
}

.career-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: var(--primary-color);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.career-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  border-color: rgba(99, 102, 241, 0.2);
}

.career-card:hover::before {
  opacity: 1;
}

.career-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
}

.career-card h3 {
  font-size: 1.25rem;
  color: var(--text-primary);
  margin: 0 0 0.25rem;
  font-weight: 600;
}

.career-period {
  background: rgba(99, 102, 241, 0.1);
  color: var(--primary-color);
  font-size: 0.8rem;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-weight: 500;
}

.career-company {
  font-size: 1rem;
  color: var(--primary-color);
  margin: 0 0 1rem;
  font-weight: 500;
}

.career-description {
  color: var(--text-secondary);
  font-size: 0.95rem;
  line-height: 1.6;
  margin-bottom: 1.25rem;
}

.career-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.skill-tag {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-secondary);
  font-size: 0.75rem;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.2s ease;
}

.career-card:hover .skill-tag {
  background: rgba(99, 102, 241, 0.1);
  color: var(--primary-color);
  border-color: rgba(99, 102, 241, 0.2);
}

.career-card-decoration {
  position: absolute;
  top: 2rem;
  left: -1.5rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timeline-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--primary-color);
  border: 2px solid var(--bg-primary);
  position: relative;
  z-index: 2;
}

.timeline-line {
  flex-grow: 1;
  width: 2px;
  background: linear-gradient(to bottom, var(--primary-color), transparent);
  margin: 0.5rem 0;
  opacity: 0.3;
}

/* Responsive Design */
@media (max-width: 768px) {
  .career-tabs {
    flex-direction: column;
    align-items: stretch;
  }
  
  .career-card {
    padding: 1.5rem 1.25rem 1.5rem 2rem;
  }
  
  .career-card-header {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .career-period {
    align-self: flex-start;
  }
}

/* Footer - Simplified to show only copyright */
.footer {
  background-color: var(--bg-secondary);
  border-top: 1px solid var(--border-color);
  padding: 1.5rem 0;
  text-align: center;
  margin-top: 3rem;
}

.footer-bottom {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.footer-bottom p {
  margin: 0;
  font-size: 0.9rem;
  color: var(--text-muted);
}

/* Hide all other footer elements */
.footer-content,
.footer-section,
.footer-socials {
  display: none;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.hero.visible {
  animation: fadeInUp 1s ease;
}

.animate-on-scroll {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s ease;
}

.animate-on-scroll.animate-in {
  opacity: 1;
  transform: translateY(0);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .hero-content {
    grid-template-columns: 1fr;
    text-align: center;
    padding: 0 var(--space-md);
  }
  
  .contact-content-centered {
    max-width: 100%;
    padding: 0 var(--space-md);
  }
}

@media (max-width: 768px) {
  :root {
    --space-xl: 2rem;
    --space-2xl: 3rem;
  }
  
  .header {
    padding: var(--space-sm) var(--space-md);
  }
  
  .header .nav {
    display: none;
  }
  
  .header .logo {
    position: static;
    margin-bottom: var(--space-sm);
  }
  
  .theme-toggle {
    position: static;
    margin-top: var(--space-sm);
  }
  
  .hero {
    padding: var(--space-xl) var(--space-md);
  }
  
  .hero-text h1 {
    font-size: var(--font-size-4xl);
  }
  
  .hero-text .title {
    font-size: var(--font-size-2xl);
  }
  
  .profile-pic {
    width: 250px;
    height: 250px;
  }
  
  .hero-stats {
    justify-content: center;
  }
  
  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .btn {
    width: 100%;
    max-width: 300px;
    justify-content: center;
  }
  
  .about-highlights {
    grid-template-columns: 1fr;
  }
  
  .timeline::before {
    left: 15px;
  }
  
  .timeline-item {
    padding-left: 50px;
  }
  
  .timeline-marker {
    left: 6px;
    width: 18px;
    height: 18px;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  .chalisa-single {
    max-width: 100%;
    padding: 0 var(--space-md);
    width: 100%;
  }
  
  .sanskrit-text {
    font-size: var(--font-size-lg);
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    text-align: center;
    width: 100%;
    padding: 0 var(--space-md);
  }
  
  .footer-socials {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .hero-text h1 {
    font-size: var(--font-size-3xl);
  }
  
  .profile-pic {
    width: 200px;
    height: 200px;
  }
  
  .hero-stats {
    flex-direction: column;
    gap: var(--space-md);
  }
  
  .project-filters {
    flex-direction: column;
    align-items: center;
  }
  
  .filter-btn {
    width: 100%;
    max-width: 200px;
  }
}

/* Utility Classes */
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }

.mb-sm { margin-bottom: var(--space-sm); }
.mb-md { margin-bottom: var(--space-md); }
.mb-lg { margin-bottom: var(--space-lg); }
.mb-xl { margin-bottom: var(--space-xl); }

.mt-sm { margin-top: var(--space-sm); }
.mt-md { margin-top: var(--space-md); }
.mt-lg { margin-top: var(--space-lg); }
.mt-xl { margin-top: var(--space-xl); }

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
  width: 100%;
  margin: 0;
  padding: 0;
}

/* Focus styles for accessibility */
*:focus {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}

/* Selection styles */
::selection {
  background-color: var(--primary-color);
  color: var(--text-inverse);
}

/* ===== Skills & Technologies â€” new design ===== */
.skills {
  padding: 28px 20px;
  margin-top: 18px;
}

.skills .section-header h2 {
  margin-bottom: 6px;
  color: var(--white);
}
.skills .section-subtitle {
  color: var(--muted);
  margin-bottom: 18px;
}

/* Grid of category cards */
.skills-grid{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 18px;
  align-items: start;
}

/* Glass card look for each skill category */
.skill-category{
  position: relative;
  padding: 18px;
  border-radius: 14px;
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border: 1px solid rgba(255,255,255,0.04);
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(2,2,8,0.6);
  transition: transform 0.22s ease, box-shadow 0.22s ease;
}
.skill-category:hover{
  transform: translateY(-8px);
  box-shadow: 0 18px 40px rgba(138,75,255,0.12);
}
.skill-category::after{
  content: "";
  position: absolute;
  inset: -40% -40%;
  background: radial-gradient(circle at 10% 20%, rgba(138,75,255,0.06), transparent 8%),
              radial-gradient(circle at 90% 80%, rgba(94,234,212,0.04), transparent 10%);
  pointer-events: none;
  mix-blend-mode: screen;
}

/* header row inside card */
.skill-header{
  display:flex;
  align-items:center;
  justify-content:space-between;
  gap:12px;
  margin-bottom:12px;
}
.skill-header h3{
  margin:0;
  color:var(--white);
  font-size:16px;
  letter-spacing:0.2px;
}
.skill-count{
  font-weight:700;
  font-size:12px;
  color:var(--accent);
  background: rgba(138,75,255,0.10);
  padding:6px 9px;
  border-radius: 999px;
  border: 1px solid rgba(138,75,255,0.12);
}

/* list of skill items inside card */
.skill-items{
  display:flex;
  flex-direction:column;
  gap:10px;
}

/* each skill row */
.skill-item{
  display:flex;
  align-items:center;
  gap:12px;
  padding:8px;
  border-radius:10px;
  background: linear-gradient(90deg, rgba(255,255,255,0.01), rgba(255,255,255,0.00));
  transition: transform 0.18s ease, box-shadow 0.18s ease, background 0.18s ease;
}
.skill-item:hover{
  transform: translateY(-6px);
  background: linear-gradient(90deg, rgba(138,75,255,0.04), rgba(94,234,212,0.02));
  box-shadow: 0 8px 18px rgba(138,75,255,0.06);
}

/* skill name */
.skill-name{
  min-width: 140px;
  color: var(--white);
  font-weight:600;
  font-size:14px;
}

/* progress bar container */
.skill-bar{
  flex:1;
  height:10px;
  background: rgba(255,255,255,0.04);
  border-radius:999px;
  overflow:hidden;
  position:relative;
  border: 1px solid rgba(255,255,255,0.02);
}

/* animated progress fill â€” existing JS sets width */
.skill-progress{
  height:100%;
  width:0%;
  border-radius:999px;
  background: linear-gradient(90deg, var(--accent), #5eead4 70%);
  box-shadow: inset 0 -3px 8px rgba(0,0,0,0.35), 0 6px 18px rgba(138,75,255,0.08);
  transition: width 1s cubic-bezier(.2,.9,.2,1);
  position:relative;
  overflow:hidden;
}

/* subtle shimmer on progress */
.skill-progress::before{
  content:"";
  position:absolute;
  inset:0;
  background: linear-gradient(90deg, rgba(255,255,255,0.06), rgba(255,255,255,0.02), rgba(255,255,255,0.06));
  transform: translateX(-110%);
  animation: shimmer 1.6s linear infinite;
  opacity: 0.5;
}
@keyframes shimmer {
  to { transform: translateX(110%); }
}

/* small tech tag alternative (if used) */
.tech-tag{
  display:inline-block;
  padding:6px 8px;
  background: rgba(255,255,255,0.02);
  color:var(--muted);
  border-radius:8px;
  font-size:12px;
  border:1px solid rgba(255,255,255,0.02);
}

/* responsive tweaks */
@media (max-width:720px){
  .skills-grid{ gap:12px; grid-template-columns: 1fr; }
  .skill-name{ min-width: 110px; font-size:13px; }
  .skill-bar{ height:8px; }
}
